import React, { useState, useEffect } from "react";
import sanityClient from "../client.js";
import { Link } from "react-router-dom";
import Loader from "react-loader-spinner";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";

export default function Posts() {
  const [postData, setPost] = useState(null);

  useEffect(() => {
    sanityClient
      .fetch(
        `*[_type == "post"]{
         title,
         slug,
         mainImage{
           asset->{
             _id,
             url
           },
           alt
         }
      }`
      )
      .then(data => setPost(data))
      .catch(console.error);
  }, []);

  let noPost = "";
  if (postData) {
    noPost = (
      <h3 className="text-lg flex justify-center cursive mt-10">
        No Post - feel free to contact me if you want to contribute here{" "}
      </h3>
    );
    console.log("ghgttg " + postData);
  }

  return (
    <main className="bg-gray-100 min-h-screen p-24">
      <section className="container mx-auto">
        <h1 className="text-5xl flex justify-center cursive mt-10">
          Blog Posts Page
        </h1>
        <h2 className="text-lg text-gray-600 flex justify-center mb-12 ">
          Welcome to my page of blog posts
        </h2>
        {noPost}
        {!postData && (
          <div className="grid justify-items-center">
            <Loader type="Puff" color="#00BFFF" height={100} width={100} />
          </div>
        )}
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
          {postData &&
            postData.map((post, index) => (
              <article>
                <Link to={"/post/" + post.slug.current} key={post.slug.current}>
                  <span
                    className="block h-64 relative rounded shadow leading-snug bg-white border-l-8 border-green-400"
                    key={index}
                  >
                    <img
                      src={post.mainImage.asset.url}
                      alt={post.mainImage.alt}
                      className="w-full h-full rounded-r object-cover absolute"
                    />
                    <span className="block relative h-full flex justify-end items-end pr-4 pb-4">
                      <h3 className="text-gray-800 text-lg font-blog px-3 py-4 bg-red-700 text-red-100 bg-opacity-75 rounded">
                        {post.title}
                      </h3>
                    </span>
                  </span>
                </Link>
              </article>
            ))}
        </div>
      </section>
    </main>
  );
}
