import React, { useState, useEffect } from "react";
import sanityClient from "../client";
import imageUrlBuilder from "@sanity/image-url";
import BlockContent from "@sanity/block-content-to-react";
import { SocialIcon } from "react-social-icons";
import Loader from "react-loader-spinner";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";

const builder = imageUrlBuilder(sanityClient);
function urlFor(source) {
  return builder.image(source);
}

export default function About() {
  const [author, setAuthor] = useState(null);

  useEffect(() => {
    sanityClient
      .fetch(
        `*[_type == "author"]{
        name,
        bio ,
        "authorImage": image.asset->url
   }`
      )
      .then(data => setAuthor(data[0]))
      .catch(console.error);
  }, []);

  return (
    <main
      className="bg-green-100 text-gray-100 min-h-screen p-10 antialiased "
      style={{
        backgroundImage:
          'url("https://images.unsplash.com/photo-1587133599421-40a3cd84831b?ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mjd8fGRlc2t0b3AlMjBjb21wdXRlcnxlbnwwfHwwfHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60")'
      }}
    >
      {author && (
        <div className="max-w-4xl flex items-center h-auto lg:h-screen flex-wrap mx-auto my-32 lg:my-0">
          <div
            id="profile"
            className="w-full lg:w-3/5 rounded-lg lg:rounded-l-lg lg:rounded-r-none shadow-2xl bg-gray-900 opacity-75 mx-6 lg:mx-0"
          >
            <div class="p-4 md:p-12 text-center lg:text-left">
              <div
                class="block lg:hidden rounded-full shadow-xl mx-auto -mt-16 h-48 w-48 bg-cover bg-center"
                style={{
                  backgroundImage:
                    'url("https://avatars.githubusercontent.com/u/57945904?v=4")'
                }}
              />
              <h1 class="text-3xl font-bold pt-8 lg:pt-0">{author.name}</h1>
              <div class="mx-auto lg:mx-0 w-4/5 pt-3 border-b-2 border-green-500 opacity-25" />
              <p class="pt-4 text-base font-bold flex items-center justify-center lg:justify-start">
                <svg
                  class="h-4 fill-current text-green-700 pr-4"
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 20 20"
                >
                  <path d="M9 12H1v6a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-6h-8v2H9v-2zm0-1H0V5c0-1.1.9-2 2-2h4V2a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v1h4a2 2 0 0 1 2 2v6h-9V9H9v2zm3-8V2H8v1h4z" />
                </svg>{" "}
                Web Developer - ML Enthusiast - Hybrid Apps
              </p>
              <p class="pt-2 text-gray-600 text-xs lg:text-sm flex items-center justify-center lg:justify-start">
                <svg
                  class="h-4 fill-current text-green-700 pr-4"
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 20 20"
                >
                  <path d="M10 20a10 10 0 1 1 0-20 10 10 0 0 1 0 20zm7.75-8a8.01 8.01 0 0 0 0-4h-3.82a28.81 28.81 0 0 1 0 4h3.82zm-.82 2h-3.22a14.44 14.44 0 0 1-.95 3.51A8.03 8.03 0 0 0 16.93 14zm-8.85-2h3.84a24.61 24.61 0 0 0 0-4H8.08a24.61 24.61 0 0 0 0 4zm.25 2c.41 2.4 1.13 4 1.67 4s1.26-1.6 1.67-4H8.33zm-6.08-2h3.82a28.81 28.81 0 0 1 0-4H2.25a8.01 8.01 0 0 0 0 4zm.82 2a8.03 8.03 0 0 0 4.17 3.51c-.42-.96-.74-2.16-.95-3.51H3.07zm13.86-8a8.03 8.03 0 0 0-4.17-3.51c.42.96.74 2.16.95 3.51h3.22zm-8.6 0h3.34c-.41-2.4-1.13-4-1.67-4S8.74 3.6 8.33 6zM3.07 6h3.22c.2-1.35.53-2.55.95-3.51A8.03 8.03 0 0 0 3.07 6z" />
                </svg>{" "}
                Location - South Africa
              </p>
              <div className="pt-8 text-sm">
                <BlockContent
                  blocks={author.bio}
                  project="8v51lno0"
                  dataset="production"
                />
              </div>

              <div class="mt-6 pb-16 lg:pb-0 w-4/5 lg:w-full mx-auto flex flex-wrap items-center justify-between">
                <SocialIcon
                  url="https://www.github.com/ntunah"
                  className="link"
                  target="_blank"
                  fgColor="#fff"
                  style={{ height: 35, width: 35 }}
                />

                <SocialIcon
                  url="https://www.linkedin.com/mwlite/in/ntuthuko-tebogo-madondo"
                  className="link"
                  target="_blank"
                  style={{ height: 35, width: 35 }}
                />

                <SocialIcon
                  url="https://www.instagram.com/ntunah.mnquhe/"
                  className="link"
                  target="_blank"
                  fgColor="#fff"
                  style={{ height: 35, width: 35 }}
                />
              </div>
            </div>
          </div>

          <div class="w-full lg:w-2/5">
            <img
              src={urlFor(author.authorImage).url()}
              className="rounded-none lg:rounded-lg shadow-2xl hidden lg:block"
            />
          </div>
        </div>
      )}

      {!author && (
        <div className="grid justify-items-center pt-48">
          <Loader type="Puff" color="#00BFFF" height={100} width={100} />
        </div>
      )}
    </main>
  );
}
